async function asyncForEach(array, callback) {
  for (let index = 0; index < array.length; index++) {
    await callback(array[index], index, array);
  }
}

export const addUserToDB = async (object, callback) => {
  let userArray = [];
  console.log(object);
  await asyncForEach(object, async (element) => {
    try {
      console.log(element);
      const email = element.email.toLowerCase();
      let unicode = element.unicode.toLowerCase().trim();

      let Id = email.replace(/[&\/\\#,+$~%.'":*?<>{}]/g, "");

      let userData = {
        email: email,
        id: Id,
        name: element.name,
        unicode: element.unicode,
        profile_image:
          "https://cdn2.iconfinder.com/data/icons/ios-7-icons/50/user_male2-512.png",
      };

      realDB.ref(`users/${Id}`).set(userData);
      userArray.push(Id);
    } catch (err) {
      console.log(err);
      userArray.push(err);
    }
  });
  console.log(userArray);
  if (callback) {
    callback();
  }
};
