import React from "react";
import PropTypes from "prop-types";
import swal from "sweetalert";

import { addUserToDB } from "./firebase";

export default class SingleUserAccountCreation extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      name: null,
      email: null,
      unicode: "penny2021",
    };
  }

  validateEmail = (email) => {
    const re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  };

  onInputChange = (event) => {
    event.preventDefault();
    let value = event.target.value;
    this.setState({ [event.target.name]: value });
  };

  handleInputChange = async (event, name) => {
    var value = this.state[name];
    if (value) value = false;
    else value = true;

    this.setState({ [name]: value });

    console.log(value);

    // Make slider instant change
    // setSingleSignIn(value);
  };

  onFormSubmit = async (event) => {
    event.preventDefault();

    var name = this.state.name;
    var email = this.state.email;
    var unicode = this.state.unicode;

    if (
      !name ||
      name.length === 0 ||
      !email ||
      email.length === 0 ||
      !unicode ||
      unicode.length === 0
    ) {
      window.alert("enter valid data");
      return;
    }

    if (!this.validateEmail(email)) {
      window.alert("enter valid email");
      return;
    }

    var obj = [
      {
        name: name,
        email: email.toLowerCase(),
        unicode: unicode.toLowerCase().trim(),
      },
    ];
    console.log(obj);
    addUserToDB(obj, () => {
      console.log("All Work is done!!!! ðŸ˜ŽðŸ˜ŽðŸ˜Ž");
      alert("User added");
      // swal({
      //   icon: "success",
      //   title: "User has been added to the database.",
      // });
      this.setState({
        name: "",
        email: "",
        unicode: "penny2021",
      });
      // window.location.reload();
    });
  };

  render() {
    return (
      <>
        <div className="card">
          <h2>Create Single User</h2>
          <div className="input-body ">
            <label htmlFor="newUserEmail">Email: </label>
            <input
              type="email"
              id="newUserEmail"
              name="email"
              placeholder="Email Address"
              value={this.state.email}
              onChange={this.onInputChange}
              autoComplete="email"
              required
            />
          </div>

          <div className="input-body ">
            <label htmlFor="newUserName">Name: </label>
            <input
              type="text"
              id="newUserName"
              name="name"
              placeholder="Name"
              value={this.state.name}
              onChange={this.onInputChange}
              required
            />
          </div>

          <div className="input-body " style={{ display: "none" }}>
            <label htmlFor="newUserCode">UniCode: </label>
            <input
              type="text"
              id="newUsercode"
              name="unicode"
              disabled={true}
              placeholder="penny2021"
              value={this.state.unicode}
              onChange={this.onInputChange}
              required
            />
          </div>

          <button theme="accent" onClick={(e) => this.onFormSubmit(e)}>
            Create User
          </button>
        </div>
      </>
    );
  }
}

