import XLSX from "xlsx";
import { realDB } from "./firebase";
import swal from "sweetalert";
import "./style.css";

export default class MultiUserAccountCreation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedFile: null,
      inUse: false,
      isLoading: false,
    };
  }
  inputRef = React.createRef();
  batchCount = 15;
  singleBatchWaitTime = 4000;
  callCounter = 0;

  asyncForEach = async (array, callback) => {
    for (let index = 0; index < array.length; index++) {
      await callback(array[index], index, array);
    }
  };

  processData = (data) => {
    // let jsonObject = JSON.stringify(newArray);
    // document.getElementById("jsonData").innerHTML = jsonObject;
    // console.log("waiting for resonse now!! request Sent ðŸ˜Ž");
    // let data = JSON.parse(jsonObject)
    // this.callCounter += 1;
    this.processFakeCloudCall(data, () => {
      console.log(this.callCounter);
      this.callCounter -= 1;
      if (this.callCounter === 0) {
        this.setState({
          inUse: false,
          isLoading: false,
          selectedFile: null,
        });
        swal({
          icon: "success",
          title: "All the users has been added to the database.",
        });
        // console.log(this.inputRef.current);
        // if (this.inputRef.current) {
        //     this.inputRef.current.files = null
        // }
      }
    });
  };

  processFakeCloudCall = async (object, callback) => {
    let userArray = [];
    console.log(object);
    await this.asyncForEach(object, async (element) => {
      try {
        const email = element.email.toLowerCase();
        // let unicode = element.unicode.toLowerCase().trim();
        let firstname = element.firstname.toLowerCase().trim();
        let lastname = element.lastname.toLowerCase().trim();
        let name = firstname + " " + lastname;
        let Id = email.replace(/[&\/\\#,+$~%.'":*?<>{}]/g, "");

        let userData = {
          email: email,
          id: Id,
          name: name,
          profile_image:
            "https://cdn2.iconfinder.com/data/icons/ios-7-icons/50/user_male2-512.png",
        };

        realDB.ref(`users/${Id}`).set(userData);
        // console.log(userData)
        userArray.push(Id);
      } catch (err) {
        console.log(err);
        userArray.push(err);
      }
    });
    console.log(userArray);
    if (callback) {
      callback();
    }
  };

  getJsonFromExcel = async (callback) => {
    var fileReader = new FileReader();
    fileReader.onload = function (event) {
      var data = event.target.result;
      var workbook = XLSX.read(data, {
        type: "binary",
      });
      workbook.SheetNames.forEach((sheet) => {
        let rowObject = XLSX.utils.sheet_to_row_object_array(
          workbook.Sheets[sheet]
        );
        callback(rowObject);
      });
    };
    fileReader.readAsBinaryString(this.state.selectedFile);
  };

  chunk = (array, size) => {
    const chunked_arr = [];
    for (let i = 0; i < array.length; i++) {
      const last = chunked_arr[chunked_arr.length - 1];
      if (!last || last.length === size) {
        chunked_arr.push([array[i]]);
      } else {
        last.push(array[i]);
      }
    }
    return chunked_arr;
  };

  takeAction = (event) => {
    if (event) {
      event.preventDefault();
    }

    if (this.state.selectedFile && !this.state.inUse) {
      this.setState({ inUse: true, isLoading: true });
      this.getJsonFromExcel((excelData) => {
        this.callCounter = 0;
        console.log(excelData);
        if (excelData.length < this.batchCount) {
          this.callCounter += 1;
          this.processData(excelData);
        } else {
          const newArray = this.chunk(excelData, this.batchCount);
          this.callCounter = 0;
          var timer = this.singleBatchWaitTime;
          newArray.forEach((array) => {
            this.callCounter += 1;
            console.log(
              array.length,
              (timer * this.callCounter) / 1000,
              this.callCounter,
              timer
            );
            setTimeout(() => {
              // console.log(newArray.indexOf(array), "--calling--");
              this.processData(array);
            }, timer * this.callCounter);
          });
        }
      });
    } else {
      console.log("wait for another task to get complete!!");
    }
  };

  render() {
    return (
      <>
        <div className="card">
          <h2>Create Users From Excel</h2>
          <div className="input-body ml">
            <label htmlFor="newUserExcel">Excel File: </label>
            <input
              type="file"
              id="newUserExcel"
              placeholder="Name"
              accept=".xls,.xlsx"
              onChange={(event) => {
                this.setState({ selectedFile: event.target.files[0] });
              }}
              ref={this.inputRef}
            />
          </div>
          <button
            disabled={!this.state.selectedFile}
            style={
              !this.state.selectedFile
                ? { pointerEvents: "none", opacity: "0.4" }
                : {}
            }
            theme="accent"
            onClick={(e) => this.takeAction(e)}
          >
            Create Users
          </button>
        </div>
      </>
    );
  }
}

